<template>
  <div class="login">
    <el-button class="login__btn" @click="back">返回首页</el-button>
    <div class="login__main">
      <div class="login__main-qrcode">
        <wxlogin
          :appid="appid"
          :scope="scope"
          :redirect_uri="redirect_uri"
          :state="state.toString()"
          :style="style"
          :href="href"
        ></wxlogin>
      </div>
      <div class="login__main-logo"><img src="../../static/img/登录.png" /></div>
    </div>
  </div>
</template>

<script>
import wxlogin from 'vue-wxlogin'
import { mapMutations } from 'vuex'

export default {
  components: {
    wxlogin
  },
  methods: {
    ...mapMutations('login', [
      'changedFapro'
    ]),
    back () {
      this.$router.push({ path: '/' })
    }
  },
  created () {
    let url = location.href // 获取url
    console.log(url)
    let arr2 = {}
    if (url.indexOf('?') !== -1) {
      let url1 = url.split('?')
      let str = url1[1].substring(0)
      let arr = str.split('&')
      for (let i = 0; i < arr.length; i++) {
        arr2[arr[i].split('=')[0]] = unescape(arr[i].split('=')[1])
      }
      this.changedFapro({
        id: arr2.id,
        label: arr2.label
      })
      console.log(arr2)
      console.log(arr2.id)
      console.log(arr2.label)
    }
  },
  data () {
    return {
      appid: 'wx60383a25b7456156',
      scope: 'snsapi_login',
      redirect_uri: 'https://ymg2.sunlightdata.cn',
      state: Math.ceil(Math.random() * 1000),
      style: 'black',
      href: 'data:text/css;base64,LmltcG93ZXJCb3ggLnFyY29kZSB7d2lkdGg6IDIwMHB4O30NCi5pbXBvd2VyQm94IC50aXRsZSB7ZGlzcGxheTogbm9uZTt9DQouaW1wb3dlckJveCAuaW5mbyB7d2lkdGg6IDIwMHB4O30NCi5zdGF0dXNfaWNvbiB7ZGlzcGxheTogbm9uZX0NCi5pbXBvd2VyQm94IC5zdGF0dXMge3RleHQtYWxpZ246IGNlbnRlcjt9'
    }
  }
}
</script>

<style lang="less" scoped>
.login {
  width: 100%;
  min-width: 1300px;
  height: 100%;
  min-height: 800px;
  background-color: #ffead9;
  &__btn {
    margin: 10px 10px 0 90%;
  }
  &__main {
    width: 1300px;
    height: 526px;
    display: flex;
    padding-top: 100px;
    margin: auto;
    &-qrcode {
      width: 680px;
      height: 526px;
      padding-top: 100px;
      background-color: #fff;
    }
    &-logo {
      width: 526px;
      height: 526px;
      img {
        width: 526px;
        height: 526px;
      }
    }
  }
}
</style>



