<template>
  <div class="lottery">
    <headpage></headpage>
    <div class="lottery__body">
      <div class="lottery__mian">
        <div class="lottery__empty" v-if="lotteryList.payload.length===0">暂无抽奖数据</div>
        <div class="lottery__lotterylist" v-if="lotteryList.payload.length>0">
          <div class="lottery__lotterylist-not" v-for="(row,i) in lotteryList.payload" :key="i">
            <div
              class="lottery__lotterylist-item"
              v-for="(cell,j) in row"
              :key="j"
              :class="((j+1) % 4 === 0)?'noright':''"
            >
              <div class="title">{{cell.lotteryName}}</div>
              <div class="time">
                <div class="time-tip">活动时间</div>
                <div class="time-text">{{cell.beginTime}}</div>
              </div>
              <div class="time">
                <div class="time-tip">至</div>
                <div class="time-text">{{cell.endTime}}</div>
              </div>
              <el-button class="btn" @click="jdlottery(cell.lotteryCode)">立即参与</el-button>
            </div>
          </div>
        </div>
        <div class="lottery__ticketlist">
          <el-button class="btn" @click="toticket">查看更多优惠券</el-button>
          <div class="lottery__ticketlist-item" @click="getTicket(ticket.key)" v-for="(ticket, idx) in fiveTicket.payload" :key="idx">
            <div class="discount"></div>
            <div class="discount-text">{{ticket.discountpercent}}折</div>
            <div class="count">限量{{ticket.batchCount}}张</div>
            <div class="cuxiao">{{ticket.coupon_name}}</div>
            <div class="range">{{ticket.limitStr}}</div>
          </div>
        </div>
      </div>
      <div class="lottery__pagination" v-if="lotteryList.payload.length > 0">
        <ul class="pagination">
          <li class="previous btn" @click="prePage" v-if="page>1">上一页</li>
          <li class="pages" :class="page===num-3?'pageActive':''" v-if="(num-3)<=lotteryList.pageCount">{{num-3}}</li>
          <li class="pages" :class="page===num-2?'pageActive':''" v-if="(num-2)<=lotteryList.pageCount">{{num-2}}</li>
          <li class="pages" :class="page===num-1?'pageActive':''" v-if="(num-1)<=lotteryList.pageCount">{{num-1}}</li>
          <li class="pages" :class="page===num?'pageActive':''" v-if="num<=lotteryList.pageCount">{{num}}</li>
          <li class="pages" :class="page===num+1?'pageActive':''" v-if="(num+1)<=lotteryList.pageCount">{{num+1}}</li>
          <li class="pages" :class="page===num+2?'pageActive':''" v-if="(num+2)<=lotteryList.pageCount">{{num+2}}</li>
          <li class="next btn" @click="nextPage" v-if="page<lotteryList.pageCount">下一页</li>
        </ul>
      </div>
      <buttompage></buttompage>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import headpage from '@/components/head'
import buttompage from '@/components/buttom'

export default {
  created () {
    console.log('进入抽奖页')
    this.changedlotteryCount(15)
    this.reloadLotteryList().catch(err => {
      console.log(err)
      if (err.message.indexOf('timeout') !== -1) {
        this.$router.push({
          path: '../notfind'
        })
      }
    })
    this.changedCount(5)
    this.loadFiveTicket()
  },
  computed: {
    ...mapState('login', [
      'loginState',
      'token'
    ]),
    ...mapState('lotterylist', [
      'lotteryList',
      'page',
      'num'
    ]),
    ...mapState('ticketlist', [
      'fiveTicket'
    ])
  },
  methods: {
    ...mapMutations('commonOp', [
      'changednav'
    ]),
    ...mapMutations('ticketlist', [
      'resetTicketState',
      'changedCount'
    ]),
    ...mapMutations('lotterylist', [
      'changedlotteryCount'
    ]),
    ...mapActions('ticketlist', [
      'reloadTicketList',
      'requiredTicket',
      'loadFiveTicket'
    ]),
    ...mapActions('lotterylist', [
      'requiredLottery',
      'reloadLotteryList',
      'loadNextLottery',
      'loadPreLottery'
    ]),
    prePage () {
      this.loadPreLottery()
    },
    nextPage () {
      this.loadNextLottery()
    },
    jdlottery (lotteryCode) {
      if (this.loginState === 0) {
        console.log('进入抽奖活动页')
        let token = this.token
        this.requiredLottery({ lotteryCode, token }).catch(err => {
          this.$message({
            message: '抽奖活动已过期',
            showClose: true,
            type: 'error'
          })
          console.log(err)
        })
      } else if (this.loginState === -400) {
        console.log('引导登录')
        this.$router.push({ path: '/pages/login?id=' + lotteryCode + '&label=lottery' })
      }
    },
    toticket () {
      this.changednav('ticket')
      this.resetTicketState()
      this.$router.push({ path: '/pages/goodticket/ticket' })
      this.changedCount(28)
      this.reloadTicketList()
    },
    getTicket (key) {
      if (this.loginState === 0) {
        let token = this.token
        this.requiredTicket({ key, token }).catch(err => {
          this.$message({
            message: '领完了~',
            showClose: true,
            type: 'error'
          })
          console.log(err)
        })
      } else if (this.loginState === -400) {
        console.log('引导登录')
        this.$router.push({ path: '/pages/login?id=' + key + '&label=ticket' })
      }
    }
  },
  components: {
    headpage,
    buttompage
  }
}
</script>

<style lang="less" scoped>
@basecolor: #ff7500;

.lottery {
  width: 100%;
  min-width: 1300px;
  background-color: #fff;
  &__body {
    position: absolute;
    top: 100px;
    width: 100%;
    min-width: 1300px;
  }
  &__mian {
    width: 1203px;
    margin: 24px auto 0 auto;
    display: flex;
  }
  .btn {
    margin-top: 10px;
    color: #fff;
    background-color: @basecolor;
    width: 282px;
    height: 30px;
    font-size: 14px;
    line-height: 10px;
    border-radius: 10px;
  }
  &__empty {
    width: 1018px;
    padding: 50px 0;
    margin: 0 10px;
    font-size: 18px;
    color: #828282;
    border: solid 1px #bfbfbf;
  }
  &__lotterylist {
    width: 1018px;
    &-not {
      display: flex;
      padding: 20px 0;
    }
    .noright {
      margin-right: 0;
    }
    &-item {
      width: 282px;
      margin-top: 10px;
      margin-right: 25px;
      .title {
        margin-bottom: 10px;
        width: 252px;
        height: 55px;
        text-align: left;
        font-size: 20px;
        color: #1d1d1d;
        word-wrap: break-word;
        word-break: break-all;
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
      }
      .time {
        display: flex;
        &-tip {
          width: 50px;
          text-align: right;
          font-size: 12px;
          color: #828282;
        }
        &-text {
          width: 200px;
          text-align: left;
          padding: 0 10px;
          font-size: 14px;
          color: @basecolor;
        }
      }
    }
  }
  &__ticketlist {
    width: 282px;
    &-item {
      cursor: pointer;
      width: 282px;
      height: 135px;
      margin-top: 13px;
      background-color: #ffd9b9;
      color: @basecolor;
      padding: 0 0 8px 0;
      .discount {
        border-style: solid;
        border-width: 60px 60px 0 0;
        border-color: @basecolor transparent transparent  transparent;
        width: 0;
        height: 0;
        &-text {
          margin-top: -50px;
          color: #fff;
          width: 40px;
          height: 23px;
          font-size: 16px;
          transform: rotate(-45deg);
          -ms-transform: rotate(-45deg); /* IE 9 */
          -moz-transform: rotate(-45deg); /* Firefox */
          -webkit-transform: rotate(-45deg); /* Safari 和 Chrome */
          -o-transform: rotate(-45deg);
        }
      }
      .count {
        margin-top: -28px;
        padding: 0 5px;
        text-align: right;
        font-size: 14px;
      }
      .cuxiao {
        margin-top: 15px;
        text-align: left;
        padding: 0 25px;
        font-size: 28px;
      }
      .range {
        text-align: left;
        padding: 0 25px;
        font-size: 14px;
      }
    }
  }
  &__pagination {
    margin-top: 50px;
    height: 80px;
    .pagination {
      .btn {
        font-size: 14px;
        padding: 10px 20px;
        border-radius: 10px;
      }
      .previous {
        border: solid 1px #333;
        background-color: #fff;
        color: #333;
      }
      .previous:hover {
        border: solid 1px @basecolor;
        color: @basecolor;
      }
      .pages {
        font-size: 14px;
        padding: 8px 15px;
        color: #333;
      }
      .pages:hover, .pageActive {
        font-size: 14px;
        padding: 8px 15px;
        color: @basecolor;
      }
    }
  }
}
</style>



