import axios from '../../axios'

export default {
  namespaced: true,
  state: {
    commodities: {
      isFetching: true,
      pageCount: 0,
      payload: []
    },
    recommendList: {
      isFetching: true,
      pageCount: 0,
      payload: []
    },
    maskarr: {
      isEnter: false,
      goodID: undefined
    },
    showtoast: false,
    commoditiesSort: [],
    keyword: '',
    type: 1,
    page: 1,
    num: 4,
    count: 8,
    getDefaultState: () => {
      const commodities = {
        isFetching: true,
        pageCount: 0,
        payload: []
      }
      const keyword = ''
      const type = 1
      const page = 1
      const num = 4
      const count = 8
      return {
        commodities,
        keyword,
        type,
        page,
        num,
        count
      }
    }
  },
  mutations: {
    resetGoodState (state) {
      Object.assign(state, state.getDefaultState())
    },
    loadingRecommend (state) {
      state.page = 1
      state.num = 4
      state.recommendList = {
        isFetching: true,
        pageCount: 0,
        payload: []
      }
    },
    loadedRecommend (state, payload) {
      state.recommendList.isFetching = false
      state.recommendList.pageCount = payload.pageCount
      state.recommendList.payload = payload.goods
    },
    reloadingCommodities (state) {
      state.page = 1
      state.num = 4
      state.commodities = {
        isFetching: true,
        pageCount: 0,
        payload: []
      }
    },
    loadingPreCommodities (state) {
      state.page = state.page - 1
      state.commodities.isFetching = true
      if (state.num > 4) {
        state.num -= 1
      }
    },
    loadingNextCommodities (state) {
      state.page = state.page + 1
      state.commodities.isFetching = true
      if (state.page === state.num && (state.page + 4) < state.commodities.pageCount) {
        state.num += 1
      }
    },
    loadedCommodities (state, payload) {
      state.commodities.isFetching = false
      state.commodities.pageCount = payload.pageCount
      state.commodities.payload = payload.goods
    },
    updategoodKeyword (state, keyword) {
      state.keyword = keyword
    },
    updateSort (state, type) {
      state.type = type
    },
    changedgoodCount (state, count) {
      state.count = count
    },
    enterItem (state, id) {
      state.maskarr.isEnter = true
      state.maskarr.goodID = id
    },
    leaveItem (state) {
      state.maskarr.isEnter = false
    },
    showtipbox (state) {
      state.showtoast = !state.showtoast
    }
  },
  actions: {
    async reloadCommodities ({ commit, state }) {
      commit('reloadingCommodities')
      let res
      res = await axios.get('/good/search?content=' + state.keyword + '&type=' + state.type + '&page=' + state.page + '&count=' + state.count)
      let result = res.data.data.goods
      let pageCount = res.data.data.pages
      let arrTemp = []
      let index = 0
      let sectionCount = 4
      for (var i = 0; i < result.length; i++) {
        index = parseInt(i / sectionCount)
        if (arrTemp.length <= index) {
          arrTemp.push([])
        }
        arrTemp[index].push(result[i])
      }
      console.log(arrTemp)
      commit('loadedCommodities', {
        pageCount,
        goods: Array.isArray(arrTemp)
          ? arrTemp
          : []
      })
      if (res.data.data.goods.length === 0) {
        commit('loadingRecommend')
        res = await axios.get('/good/search?content=&type=1&page=1&count=8')
        let result = res.data.data.goods
        let pageCount = res.data.data.pages
        let arrTemp = []
        let index = 0
        let sectionCount = 4
        for (let i = 0; i < result.length; i++) {
          index = parseInt(i / sectionCount)
          if (arrTemp.length <= index) {
            arrTemp.push([])
          }
          arrTemp[index].push(result[i])
        }
        console.log(arrTemp)
        commit('loadedRecommend', {
          pageCount,
          goods: Array.isArray(arrTemp)
            ? arrTemp
            : []
        })
        return res
      }
      return res
    },
    async loadNextCommodities ({ commit, state }) {
      if (state.page === state.commodities.pageCount) {
        return
      }
      commit('loadingNextCommodities')
      let res = await axios.get('/good/search?content=' + state.keyword + '&type=' + state.type + '&page=' + state.page + '&count=' + state.count)
      let result = res.data.data.goods
      let pageCount = res.data.data.pages
      let arrTemp = []
      let index = 0
      let sectionCount = 4
      for (var i = 0; i < result.length; i++) {
        index = parseInt(i / sectionCount)
        if (arrTemp.length <= index) {
          arrTemp.push([])
        }
        arrTemp[index].push(result[i])
      }
      console.log(arrTemp)
      commit('loadedCommodities', {
        pageCount,
        goods: Array.isArray(arrTemp)
          ? arrTemp
          : []
      })
      return res
    },
    async loadPreCommodities ({ commit, state }) {
      if (state.page === 1) {
        return
      }
      commit('loadingPreCommodities')
      let res = await axios.get('/good/search?content=' + state.keyword + '&type=' + state.type + '&page=' + state.page + '&count=' + state.count)
      let result = res.data.data.goods
      let pageCount = res.data.data.pages
      let arrTemp = []
      let index = 0
      let sectionCount = 4
      for (var i = 0; i < result.length; i++) {
        index = parseInt(i / sectionCount)
        if (arrTemp.length <= index) {
          arrTemp.push([])
        }
        arrTemp[index].push(result[i])
      }
      console.log(arrTemp)
      commit('loadedCommodities', {
        pageCount,
        goods: Array.isArray(arrTemp)
          ? arrTemp
          : []
      })
      return res
    }
  }
}



