<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import $ from 'jquery'
import headpage from '@/components/head'
import buttompage from '@/components/buttom'
import toastbox from '@/components/toastbox'

export default {
  components: {
    headpage,
    buttompage,
    toastbox
  },
  created () {
    this.resetCommonState()
    this.resetGoodState()
    this.resetTicketState()
    this.resetLotteryState()
    this.reloadCommodities().catch(err => {
      if (err.message.indexOf('timeout') !== -1) {
        this.$message({
          message: '请求超时',
          showClose: true,
          type: 'error'
        })
        this.$router.push({ path: '../pages/notfind' })
      }
    })
    this.reloadTicketList()
    this.reloadLotteryList()
    // let url = 'https://ymg2.sunlightdata.cn/?code=001aadVm07N8Sk1MBvYm0btbVm0aadVA&state=733'
    let url = location.href // 获取url
    let arr2 = {}
    if (url.indexOf('?') !== -1) {
      let url1 = url.split('?')
      let str = url1[1].substring(0)
      let arr = str.split('&')
      for (let i = 0; i < arr.length; i++) {
        arr2[arr[i].split('=')[0]] = unescape(arr[i].split('=')[1])
      }
      if (arr2.code !== undefined) {
        let code = arr2.code
        this.login(code)
          .then(res => {
            console.log('登录请求发送成功')
            console.log(res)
            if (this.isAuth.label === 'ticket') {
              let key = this.isAuth.id
              let token = this.token
              this.requiredTicket({key, token}).catch(err => {
                this.$message({
                  message: '领完了~',
                  showClose: true,
                  type: 'error'
                })
                console.log(err)
              })
            } else if (this.isAuth.label === 'lottery') {
              let lotteryCode = this.isAuth.id
              let token = this.token
              this.requiredLottery({ lotteryCode, token }).catch(err => {
                this.$message({
                  message: '抽奖活动已过期',
                  showClose: true,
                  type: 'error'
                })
                console.log(err)
              })
            }
            this.changedFapro({
              id: 0,
              label: ''
            })
          })
          .catch(err => {
            console.log('登录失败')
            console.log(err)
            if (err.message.indexOf('timeout') !== -1) {
              this.$message({
                message: '请求超时',
                showClose: true,
                type: 'error'
              })
              this.$router.push({ path: '../pages/notfind' })
            } else {
              this.$message({
                message: '登录失败',
                showClose: true,
                type: 'error'
              })
            }
          })
      }
    }
  },
  onLoad () {
    $('#myCarousel').carousel({
      interval: 1500
    })
  },
  computed: {
    ...mapState('commonOp', [
      'searchcommand',
      'searchrange',
      'keyword'
    ]),
    ...mapState('login', [
      'loginState',
      'isAuth',
      'token'
    ]),
    ...mapState('goodlist', [
      'maskarr',
      'commodities',
      'showtoast'
    ]),
    ...mapState('ticketlist', [
      'ticketList'
    ]),
    ...mapState('lotterylist', [
      'lotteryList'
    ])
  },
  methods: {
    ...mapMutations('commonOp', [
      'changedsearchRange',
      'changednav',
      'resetCommonState'
    ]),
    ...mapMutations('login', [
      'changedFapro'
    ]),
    ...mapMutations('goodlist', [
      'enterItem',
      'leaveItem',
      'showtipbox',
      'changedgoodCount',
      'updategoodKeyword',
      'resetGoodState'
    ]),
    ...mapMutations('ticketlist', [
      'resetTicketState',
      'updateKeyword',
      'changedCount'
    ]),
    ...mapMutations('lotterylist', [
      'resetLotteryState',
      'changedlotteryCount'
    ]),
    ...mapActions('login', [
      'login'
    ]),
    ...mapActions('goodlist', [
      'reloadCommodities'
    ]),
    ...mapActions('ticketlist', [
      'loadFiveTicket',
      'reloadTicketList',
      'requiredTicket'
    ]),
    ...mapActions('lotterylist', [
      'reloadLotteryList',
      'requiredLottery'
    ]),
    handleCommand (command) {
      this.changedsearchRange(command)
    },
    searchkey (e) {
      console.log(e.target.value)
      if (this.searchcommand === 'goods') {
        this.updategoodKeyword(e.target.value)
      } else if (this.searchcommand === 'ticket') {
        this.updateKeyword(e.target.value)
      }
    },
    pressEnter (e) {
      let keyCode = window.event ? e.keyCode : e.which
      console.log('回车搜索', keyCode, e)
      if (keyCode === 13) {
        console.log('执行搜索')
        this.handlesearch()
      }
    },
    handlesearch () {
      if (this.searchcommand === 'goods') {
        this.changednav('price')
        this.$router.push({ path: '/pages/goodprice/goodprice' })
        this.changedgoodCount(28)
        this.reloadCommodities()
      } else if (this.searchcommand === 'ticket') {
        this.changednav('ticket')
        this.$router.push({ path: '/pages/goodticket/ticket' })
        this.changedCount(28)
        this.reloadTicketList()
      }
    },
    mouseEnter (id) {
      this.enterItem(id)
    },
    mouseLeave () {
      this.leaveItem()
    },
    gooddetail (url) {
      console.log(url)
      window.open(url, '_blank')
    },
    showtoastbox () {
      this.showtipbox()
    },
    togoodprice () {
      this.changednav('price')
      this.resetGoodState()
      this.$router.push({ path: '/pages/goodprice/goodprice' })
      this.changedgoodCount(28)
      this.reloadCommodities()
    },
    toticket () {
      this.changednav('ticket')
      this.resetTicketState()
      this.$router.push({ path: '/pages/goodticket/ticket' })
      this.changedCount(28)
      this.reloadTicketList()
    },
    getTicket (key) {
      if (this.loginState === 0) {
        let token = this.token
        this.requiredTicket({ key, token }).catch(err => {
          this.$message({
            message: '领完了~',
            showClose: true,
            type: 'error'
          })
          console.log(err)
        })
      } else if (this.loginState === -400) {
        console.log('引导登录')
        this.$router.push({ path: '/pages/login?id=' + key + '&label=ticket' })
      }
    },
    tolottery () {
      this.changednav('lottery')
      this.resetLotteryState()
      this.$router.push({ path: '/pages/lottery/lottery' })
      this.changedlotteryCount(15)
      this.reloadLotteryList()
      this.changedCount(5)
      this.loadFiveTicket()
    },
    jdlottery (lotteryCode) {
      if (this.loginState === 0) {
        console.log('进入抽奖活动页')
        let token = this.token
        this.requiredLottery({ lotteryCode, token }).catch(err => {
          this.$message({
            message: '抽奖活动已过期',
            showClose: true,
            type: 'error'
          })
          console.log(err)
        })
      } else if (this.loginState === -400) {
        console.log('引导登录')
        this.$router.push({ path: '/pages/login?id=' + lotteryCode + '&label=lottery' })
      }
    }
  }
}
</script>
