<template>
  <div class="goodticket">
    <headpage></headpage>
    <div class="goodticket__body">
    <div class="goodticket__searchbox">
      <div class="goodticket__searchbox-input"><input placeholder="请输入关键字" :value="keyword" @input="searchkey" @keyup.enter="pressEnter" /></div>
      <div class="goodticket__searchbox-btn"><el-button @click="searchHandle">搜索</el-button></div>
    </div>
    <div class="goodticket__shift">
      <div class="goodticket__shift-sort">
        <div class="goodticket__shift-sort-title">优惠券排序:</div>
        <div class="goodticket__shift-sort-btn">
          <el-button size="small" @click="changedsort(1)" :class="type===1? 'sortactive': ''">按最新</el-button>
          <el-button size="small" @click="changedsort(2)" :class="type===2? 'sortactive': ''">按折扣</el-button>
          <el-button size="small" @click="changedsort(3)" :class="type===3? 'sortactive': ''">按省钱</el-button>
        </div>
      </div>
    </div>
    <div class="goodticket__list" v-if="ticketList.payload.length > 0">
      <div class="goodticket__list-not" v-for="(row,i) in ticketList.payload" :key="i">
        <div
          class="goodticket__list-item"
          v-for="(cell,j) in row"
          :key="j"
          :class="((j+1) % 4 === 0)?'noright':''"
          @click="getTicket(cell.key)"
        >
          <div class="discount"></div>
          <div class="discount-text">{{cell.discountpercent}}折</div>
          <div class="count">限量{{cell.batchCount}}张</div>
          <div class="cuxiao">{{cell.coupon_name}}</div>
          <div class="range">{{cell.limitStr}}</div>
        </div>
      </div>
    </div>
    <div class="goodticket__pagination" v-if="ticketList.payload.length > 0">
      <ul class="pagination">
        <li class="previous btn" @click="prePage" v-if="page>1">上一页</li>
        <li class="pages" :class="page===num-3?'pageActive':''" v-if="(num-3)<=ticketList.pageCount">{{num-3}}</li>
        <li class="pages" :class="page===num-2?'pageActive':''" v-if="(num-2)<=ticketList.pageCount">{{num-2}}</li>
        <li class="pages" :class="page===num-1?'pageActive':''" v-if="(num-1)<=ticketList.pageCount">{{num-1}}</li>
        <li class="pages" :class="page===num?'pageActive':''" v-if="num<=ticketList.pageCount">{{num}}</li>
        <li class="pages" :class="page===num+1?'pageActive':''" v-if="(num+1)<=ticketList.pageCount">{{num+1}}</li>
        <li class="pages" :class="page===num+2?'pageActive':''" v-if="(num+2)<=ticketList.pageCount">{{num+2}}</li>
        <li class="next btn" @click="nextPage" v-if="page<ticketList.pageCount">下一页</li>
      </ul>
    </div>
    <div class="goodticket__list-empty" v-if="ticketList.payload.length===0">
      <div class="goodticket__list-empty-tip">抱歉，你搜索的优惠券暂时没有找到哦~</div>
      <div class="goodticket__list-empty-recommend">
        <div class="goodticket__list-empty-recommend-top">
          <div class="goodticket__list-empty-recommend-top-title">推荐优惠券</div>
          <div class="goodticket__list-empty-recommend-top-more" @click="moreTicket">查看更多<i class="fa fa-angle-double-right" aria-hidden="true"></i></div>
        </div>
        <div class="goodticket__list-empty-recommend-line"></div>
        <div class="goodticket__list-empty-recommend-list" v-for="(row,i) in recommendList.payload" :key="i">
          <div
            class="goodticket__list-item"
            v-for="(cell,j) in row"
            :key="j"
            :class="((j+1) % 4 === 0)?'noright':''"
            @click="getTicket(cell._id)"
          >
            <div class="discount"></div>
            <div class="discount-text">{{cell.discountpercent}}折</div>
            <div class="count">限量{{cell.batchCount}}张</div>
            <div class="cuxiao">{{cell.coupon_name}}</div>
            <div class="range">{{cell.limitStr}}</div>
          </div>
        </div>
      </div>
    </div>
    <buttompage></buttompage>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import headpage from '@/components/head'
import buttompage from '@/components/buttom'

export default {
  created () {
    console.log('进入神券页')
    this.changedCount(28)
    this.reloadTicketList().catch(err => {
      console.log(err)
      if (err.message.indexOf('timeout') !== -1) {
        this.$router.push({
          path: '../notfind'
        })
      }
    })
  },
  components: {
    headpage,
    buttompage
  },
  computed: {
    ...mapState('login', [
      'loginState',
      'token'
    ]),
    ...mapState('ticketlist', [
      'ticketList',
      'recommendList',
      'type',
      'keyword',
      'page',
      'num'
    ])
  },
  methods: {
    ...mapMutations('ticketlist', [
      'updateSort',
      'updateKeyword',
      'resetTicketState',
      'changedCount'
    ]),
    ...mapActions('ticketlist', [
      'reloadTicketList',
      'loadNextTicketList',
      'loadPreTicketList',
      'requiredTicket'
    ]),
    searchkey (e) {
      console.log(e.target.value)
      this.updateKeyword(e.target.value)
    },
    pressEnter (e) {
      let keyCode = window.event ? e.keyCode : e.which
      console.log('回车搜索', keyCode, e)
      if (keyCode === 13) {
        console.log('执行搜索')
        this.searchHandle()
      }
    },
    searchHandle () {
      this.changedCount(28)
      this.reloadTicketList()
    },
    changedsort (type) {
      this.updateSort(type)
      this.reloadTicketList()
    },
    prePage () {
      this.loadPreTicketList()
    },
    nextPage () {
      this.loadNextTicketList()
    },
    moreTicket () {
      this.resetTicketState()
      this.changedCount(28)
      this.reloadTicketList()
    },
    getTicket (key) {
      if (this.loginState === 0) {
        let token = this.token
        this.requiredTicket({ key, token }).catch(err => {
          this.$message({
            message: '领完了~',
            showClose: true,
            type: 'error'
          })
          console.log(err)
        })
      } else if (this.loginState === -400) {
        console.log('引导登录')
        this.$router.push({ path: '/pages/login?id=' + key + '&label=ticket' })
      }
    }
  }
}
</script>

<style lang="less" scoped>
@basecolor: #ff7500;

.goodticket {
  width: 100%;
  min-width: 1300px;
  background-color: #fff;
  &__body {
    position: absolute;
    top: 100px;
    width: 100%;
    min-width: 1300px;
  }
  &__searchbox {
    margin: 20px auto;
    width: 795px;
    height: 50px;
    border-radius: 10px;
    display: flex;
    border-radius: 10px;
    border: solid 1px #eee;
    &-input {
      padding-left: 8px;
    }
    input {
      font-size: 16px;
      width: 660px;
      text-align: center;
      height: 48px;
      padding: 3px 5px;
      border: none;
    }
    input::placeholder {
      color: #bebebe;
    }
    &-btn {
      width: 120px;
      height: 48px;
      background-color: @basecolor;
    }
    button {
      margin-left: 8px;
      padding-left: 10px;
      width: 120px;
      height: 48px;
      color: #fff;
      border: none;
      background-color: @basecolor;
      border-radius: 10px;
    }
  }
  &__shift {
    width: 1203px;
    margin: 0 auto 0 auto;
    &-sort {
      margin-top: 24px;
      margin-left: 20px;
      display: flex;
      font-size: 16px;
      color: #979797;
      &-title {
        width: 85px;
        text-align: left;
        margin-right: 10px;
      }
      &-btn {
        button {
          font-size: 16px;
          color: #979797;
          padding: 0 0;
          border: none;
        }
        button:hover, .sortactive{
          color: @basecolor;
          background-color: #fff;
        }
      }
    }
  }
  &__list {
    width: 1203px;
    margin: 32px auto 0 auto;
    border-top: solid 1px #bebebe;
    padding: 20px 0;
    &-not {
      margin-top: 10px;
      display: flex;
    }
    .noright {
      margin-right: 0;
    }
    &-item {
      width: 282px;
      height: 135px;
      margin-right: 25px;
      background-color: #ffd9b9;
      color: @basecolor;
      padding: 0 0 8px 0;
      .discount {
        border-style: solid;
        border-width: 60px 60px 0 0;
        border-color: @basecolor transparent transparent  transparent;
        width: 0;
        height: 0;
        &-text {
          margin-top: -50px;
          color: #fff;
          width: 40px;
          height: 23px;
          font-size: 16px;
          transform: rotate(-45deg);
          -ms-transform: rotate(-45deg); /* IE 9 */
          -moz-transform: rotate(-45deg); /* Firefox */
          -webkit-transform: rotate(-45deg); /* Safari 和 Chrome */
          -o-transform: rotate(-45deg);
        }
      }
      .count {
        margin-top: -28px;
        padding: 0 5px;
        text-align: right;
        font-size: 14px;
      }
      .cuxiao {
        margin-top: 15px;
        text-align: left;
        padding: 0 25px;
        font-size: 28px;
      }
      .range {
        text-align: left;
        padding: 0 25px;
        font-size: 14px;
      }
    }
    &-empty {
      width: 1300px;
      margin: 20px auto;
      border-top: solid 1px #bebebe;
      &-tip {
        margin: 44px 0;
        font-size: 20px;
        color: #555;
        text-align: left;
      }
      &-recommend {
        &-list {
          margin-top: 10px;
          display: flex;
        }
        &-top {
          display: flex;
          &-title {
            font-size: 26px;
            color: #2d2d2d;
            text-align: left;
          }
          &-more {
            width: 80px;
            height: 50px;
            line-height: 70px;
            margin-left: auto;
            font-size: 14px;
            color: #2d2d2d;
            cursor: pointer;
          }
        }
        &-line {
          margin: 5px 0 18px 0;
          width: 55px;
          height: 5px;
          background-color: @basecolor;
          border-radius: 5px;
        }
      }
    }
  }
  &__pagination {
    height: 80px;
    .pagination {
      .btn {
        font-size: 14px;
        padding: 10px 20px;
        border-radius: 10px;
      }
      .previous {
        border: solid 1px #333;
        background-color: #fff;
        color: #333;
      }
      .previous:hover {
        border: solid 1px @basecolor;
        color: @basecolor;
      }
      .next {
        color: #fff;
        background-color: @basecolor;
      }
      .pages {
        font-size: 14px;
        padding: 8px 15px;
        color: #333;
      }
      .pages:hover, .pageActive {
        font-size: 14px;
        padding: 8px 15px;
        color: @basecolor;
      }
    }
  }
}
</style>



