<template>
  <div class="notfind">
    <div class="notfind__btn">
      <el-button class="notfind__btn-pre" @click="back">返回上一页</el-button>
      <el-button class="notfind__btn-home" @click="toindex">返回首页</el-button>
    </div>
  </div>
</template>
<script>
export default {
  methods: {
    back () {
      this.$router.go(-1)
    },
    toindex () {
      this.$router.push({ path: '/' })
    }
  }
}
</script>

<style lang="less" scoped>
.notfind {
  width: 100%;
  min-width: 1300px;
  height: 100%;
  min-height: 800px;
  background-image: url("../img/404.png");
  &__btn {
    padding-top: 647px;
    width: 1300px;
    margin: 0 auto;
    button {
      border-radius: 10px;
    }
    &-pre {
      background-color: #ff7500;
      color: #fff;
    }
    &-home {
      margin-left: 50px;
      border: solid 1px #ff7500;
      color: #ff7500;
    }
  }
}
</style>



