<template>
  <div class="head">
    <div class="head__left">
      <div class="head__left-name" @click="toindex">天天特价</div>
    </div>
    <div class="head__right">
      <div class="head__right-nav">
        <div class="head__right-nav-item" :class="navstate==='index'? 'active':''" @click="toindex">首页</div>
        <div class="head__right-nav-item" :class="navstate==='price'? 'active':''" @click="togoodprice">神价</div>
        <div class="head__right-nav-item" :class="navstate==='ticket'? 'active':''" @click="toticket">优惠券</div>
        <div class="head__right-nav-item" :class="navstate==='lottery'? 'active':''" @click="tolottery">抽奖</div>
        <div v-if="loginState!==0" class="head__right-nav-login" id="qrcode" @click="loginHandle"><img src="../../static/img/登录1.png" /><span>登录</span></div>
        <div v-if="loginState===0" class="head__right-nav-logout">
          <div class="img"><img :src="userinfo.payload.headimgurl" /></div>
          <el-dropdown class="dropdownbox" @command="handleCommand">
            <div class="el-dropdown-link name">{{userinfo.payload.nickname}}<i class="fa fa-caret-down" aria-hidden="true"></i></div>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item icon="el-icon-plus" class="dropitem" command='logout'>注销登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'

export default {
  computed: {
    ...mapState('commonOp', [
      'navstate'
    ]),
    ...mapState('login', [
      'loginState',
      'userinfo'
    ])
  },
  methods: {
    ...mapMutations('commonOp', [
      'changednav'
    ]),
    ...mapMutations('login', [
      'resetloginState'
    ]),
    toindex () {
      this.changednav('index')
      this.$router.push({ path: '/' })
    },
    togoodprice () {
      this.changednav('price')
      this.$router.push({ path: '/pages/goodprice/goodprice' })
    },
    toticket () {
      this.changednav('ticket')
      this.$router.push({ path: '/pages/goodticket/ticket' })
    },
    tolottery () {
      this.changednav('lottery')
      this.$router.push({ path: '/pages/lottery/lottery' })
    },
    loginHandle () {
      this.$router.push({ path: '/pages/login' })
    },
    handleCommand (command) {
      if (command === 'logout') {
        this.resetloginState()
        this.$message({
          message: '注销成功',
          showClose: true,
          type: 'success'
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
@basecolor: #ff7500;
.head {
  position: fixed;
  z-index: 200;
  width: 100%;
  min-width: 800px;
  height: 100px;
  display: flex;
  margin: 0 auto;
  background-color: #fff;
  box-shadow:4px 2px 8px #bfbfbf;
  -webkit-box-shadow:4px 2px 8px #bfbfbf;
  -moz-box-shadow:3px 2px 8px #bfbfbf;
  &__left {
    width: 50%;
    min-width: 200px;
    &-name {
      width: 300px;
      height: 100px;
      line-height: 100px;
      font-size: 36.56px;
      color: @basecolor;
      text-align: left;
      margin-left: 20px;
    }
  }
  &__right {
    width: 50%;
    min-width: 600px;
    &-nav {
      display: flex;
      margin-left: auto;
      width: 600px;
      height: 100px;
      line-height: 100px;
      .active {
        background-color: @basecolor;
        color: #fff;
      }
      &-item {
        cursor: pointer;
        font-size: 16px;
        width: 90px;
        button {
          margin: 0 10px;
        }
      }
      &-login {
        width: 120px;
        padding-left: 25px;
        font-size: 16px;
        span {
          cursor: pointer;
          margin: 0 30px 0 10px;
        }
      }
      &-logout {
        padding: 0 10px;
        display: flex;
        .img {
          width: 30px;
          height: 30px;
          img {
            width: 30px;
            height: 30px;
            border-radius: 15px;
          }
        }
        .dropdownbox {
          height: 60px;
        }
        .name {
          padding-left: 5px;
          font-size: 14px;
          color: #333;
          i {
            padding-left: 5px;
          }
        }
        .name:hover {
          color: @basecolor;
        }
      }
    }
  }
}
</style>



