<template>
  <div class="box">
    <div class="box__title">
      {{baseinfo.payload[4].k}}?【
      <span class="box__title-light">{{baseinfo.payload[4].v}}</span>
      】
    </div>
    <div class="box__step">
      <div class="box__step-title">第一步</div>
      <div class="box__step-content">
        关注公众号【
        <span class="box__step-content-light">{{baseinfo.payload[5].v}}</span>
        】
      </div>
      <div class="box__step-img"><img :src="baseinfo.payload[3].v" /></div>
    </div>
    <div class="box__step">
      <div class="box__step-title">第二步</div>
      <div class="box__step-content">
        回复关键词【
        <span class="box__step-content-light">{{baseinfo.payload[6].v}}</span>
        】
      </div>
    </div>
    <div class="box__bottom">
      <div class="box__bottom-service">
        <div class="box__bottom-service-tip">如有疑问添加客服微信</div>
        <div class="box__bottom-service-img"><img :src="baseinfo.payload[2].v" /></div>
      </div>
    </div>
    <el-button @click="confirm">确认</el-button>
  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'

export default {
  computed: {
    ...mapState('commonOp', [
      'baseinfo'
    ])
  },
  methods: {
    ...mapMutations('goodlist', [
      'showtipbox'
    ]),
    confirm () {
      this.showtipbox()
    }
  }
}
</script>

<style lang="less" scoped>
@basecolor: #ff7500;

.box {
  width: 764px;
  height: 764px;
  background-color: #fff;
  border: solid 1px #bfbfbf;
  margin: 0 auto;
  padding-top: 43px;
  &__title {
    display: flex;
    width: 500px;
    margin: 0 auto;
    font-size: 26px;
    color: #000;
    span {
      max-width: 265px;
      color: @basecolor;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      -o-text-overflow: ellipsis;
    }
  }
  &__step {
    &-title {
      margin-top: 43px;
      margin-bottom: 10px;
      font-size: 16px;
      color: #000;
    }
    &-content {
      font-size: 16px;
      color: #000;
      margin: 0 auto;
      span {
        color: @basecolor;
      }
    }
    &-img {
      margin-top: 30px;
      img {
        width: 100px;
        height: 100px;
        background-color: #333;
      }
    }
  }
  &__bottom {
    width: 680px;
    margin: 35px auto 0 auto;
    border-top: solid 1px #bebebe;
    &-service {
      margin-top: 20px;
      &-tip {
        font-size: 16px;
        color: #000;
      }
      &-img {
        margin-top: 20px;
        img {
          width: 100px;
          height: 100px;
          background-color: #333;
        }
      }
    }
  }
  button {
    color: #fff;
    margin-top: 80px;
    width: 232px;
    height: 42px;
    background-color: @basecolor;
  }
}
</style>



