import axios from '../../axios'
import { MessageBox } from 'element-ui'

export default {
  namespaced: true,
  state: {
    fiveTicket: {
      isFetching: true,
      payload: []
    },
    ticketList: {
      isFetching: true,
      pageCount: 0,
      payload: []
    },
    recommendList: {
      isFetching: true,
      pageCount: 0,
      payload: []
    },
    ticketUrl: {
      isFetching: true,
      url: ''
    },
    keyword: '',
    type: 1,
    page: 1,
    count: 8,
    num: 4,
    getDefaultState: () => {
      const ticketList = {
        isFetching: true,
        pageCount: 0,
        payload: []
      }
      const keyword = ''
      const type = 1
      const page = 1
      const count = 8
      const num = 4
      return {
        ticketList,
        keyword,
        type,
        page,
        count,
        num
      }
    }
  },
  mutations: {
    resetTicketState (state) {
      Object.assign(state, state.getDefaultState())
    },
    loadingRecommend (state) {
      state.page = 1
      state.num = 4
      state.recommendList = {
        isFetching: true,
        pageCount: 0,
        payload: []
      }
    },
    loadedRecommend (state, payload) {
      state.recommendList.isFetching = false
      state.recommendList.pageCount = payload.pageCount
      state.recommendList.payload = payload.ticketList
    },
    loadingFiveTicket (state) {
      state.fiveTicket.isFetching = true
    },
    loadedFiveTicket (state, payload) {
      state.fiveTicket.isFetching = false
      state.fiveTicket.payload = payload.ticketList
    },
    reloadingTicketList (state) {
      state.page = 1
      state.num = 4
      state.ticketList = {
        isFetching: true,
        pageCount: 0,
        payload: []
      }
    },
    loadingNextTicketList (state) {
      state.page = state.page + 1
      state.ticketList.isFetching = true
      if (state.page === state.num && (state.page + 4) < state.ticketList.pageCount) {
        state.num += 1
      }
    },
    loadingPreTicketList (state) {
      state.page = state.page - 1
      state.ticketList.isFetching = true
      if (state.num > 4) {
        state.num -= 1
      }
    },
    loadedTicketList (state, payload) {
      state.ticketList.isFetching = false
      state.ticketList.pageCount = payload.pageCount
      state.ticketList.payload = payload.ticketList
    },
    updateKeyword (state, keyword) {
      state.keyword = keyword
    },
    updateSort (state, type) {
      state.type = type
    },
    changedCount (state, count) {
      state.count = count
    },
    gettingUrl (state) {
      state.ticketUrl = {
        isFetching: true,
        url: ''
      }
    },
    gettedUrl (state, url) {
      state.ticketUrl.isFetching = false
      state.ticketUrl.url = url
    }
  },
  actions: {
    async reloadTicketList ({ commit, state }) {
      commit('reloadingTicketList')
      let res
      res = await axios.get('/coupon/search?content=' + state.keyword + '&type=' + state.type + '&page=' + state.page + '&count=' + state.count)
      let result = res.data.data.coupons
      let pageCount = res.data.data.pages
      let arrTemp = []
      let index = 0
      let sectionCount = 4
      for (let i = 0; i < result.length; i++) {
        index = parseInt(i / sectionCount)
        if (arrTemp.length <= index) {
          arrTemp.push([])
        }
        arrTemp[index].push(result[i])
      }
      console.log(arrTemp)
      commit('loadedTicketList', {
        pageCount,
        ticketList: Array.isArray(arrTemp)
          ? arrTemp
          : []
      })
      if (res.data.data.coupons.length === 0) {
        commit('loadingRecommend')
        res = await axios.get('/coupon/search?content=&type=2&page=1&count=8')
        let result = res.data.data.coupons
        let pageCount = res.data.data.pages
        let arrTemp = []
        let index = 0
        let sectionCount = 4
        for (let i = 0; i < result.length; i++) {
          index = parseInt(i / sectionCount)
          if (arrTemp.length <= index) {
            arrTemp.push([])
          }
          arrTemp[index].push(result[i])
        }
        console.log(arrTemp)
        commit('loadedRecommend', {
          pageCount,
          ticketList: Array.isArray(arrTemp)
            ? arrTemp
            : []
        })
        return res
      }
      console.log(res)
      return res
    },
    async loadNextTicketList ({ commit, state }) {
      if (state.page === state.ticketList.pageCount) {
        return
      }
      commit('loadingNextTicketList')
      let res = await axios.get('/coupon/search?content=' + state.keyword + '&type=' + state.type + '&page=' + state.page + '&count=' + state.count)
      let result = res.data.data.coupons
      let pageCount = res.data.data.pages
      let arrTemp = []
      let index = 0
      let sectionCount = 4
      for (var i = 0; i < result.length; i++) {
        index = parseInt(i / sectionCount)
        if (arrTemp.length <= index) {
          arrTemp.push([])
        }
        arrTemp[index].push(result[i])
      }
      console.log(arrTemp)
      commit('loadedTicketList', {
        pageCount,
        ticketList: Array.isArray(arrTemp)
          ? arrTemp
          : []
      })
      console.log(res)
      return res
    },
    async loadPreTicketList ({commit, state}) {
      if (state.page === 1) {
        return
      }
      commit('loadingPreTicketList')
      let res = await axios.get('/coupon/search?content=' + state.keyword + '&type=' + state.type + '&page=' + state.page + '&count=' + state.count)
      let result = res.data.data.coupons
      let pageCount = res.data.data.pages
      let arrTemp = []
      let index = 0
      let sectionCount = 4
      for (var i = 0; i < result.length; i++) {
        index = parseInt(i / sectionCount)
        if (arrTemp.length <= index) {
          arrTemp.push([])
        }
        arrTemp[index].push(result[i])
      }
      console.log(arrTemp)
      commit('loadedTicketList', {
        pageCount,
        ticketList: Array.isArray(arrTemp)
          ? arrTemp
          : []
      })
      console.log(res)
      return res
    },
    async requiredTicket ({commit}, { key, token }) {
      commit('gettingUrl')
      let res = await axios.post('/coupon/pc/unlock?token=' + token, {key})
      if (res.data.code === 0) {
        let url = res.data.data
        commit('gettedUrl', url)
        window.open(url, '_blank')
      } else {
        MessageBox({
          message: '页面获取失败',
          showClose: true,
          type: 'error'
        })
      }
      console.log(res)
      return res
    },
    async loadFiveTicket ({commit}) {
      commit('loadingFiveTicket')
      let res = await axios.get('/coupon/search?content=&type=3&page=1&count=5')
      let result = res.data.data.coupons
      commit('loadedFiveTicket', {
        ticketList: Array.isArray(result)
          ? result
          : []
      })
      return res
    }
  }
}



