<template>
  <div class="buttom">
    <div class="buttom__main">
      <div class="buttom__left">
        <div class="buttom__left-webname" @click="toindex">天天好价</div>
        <div class="buttom__left-item">
          <div class="buttom__left-item-list" @click="togoodprice">好价</div>
          <div class="buttom__left-item-list" @click="toticket">优惠券</div>
          <div class="buttom__left-item-list" @click="tolottery">抽奖</div>
        </div>
      </div>
      <div class="buttom__right">
        <div class="buttom__right-contact">{{baseinfo.payload[0].k}}：<span>{{baseinfo.payload[0].v}}</span></div>
        <div class="buttom__right-contact">{{baseinfo.payload[1].k}}：<span>{{baseinfo.payload[1].v}}</span></div>
        <div class="buttom__right-qrcode">
          <img :src="baseinfo.payload[2].v" />
        </div>
      </div>
    </div>
    <div class="buttom__reference">{{baseinfo.payload[7].k}}：{{baseinfo.payload[7].v}}</div>
  </div>
</template>
<script>
import { mapMutations, mapActions, mapState } from 'vuex'

export default {
  created () {
    this.getBaseinfo()
  },
  computed: {
    ...mapState('commonOp', [
      'baseinfo'
    ])
  },
  methods: {
    ...mapMutations('commonOp', [
      'changednav'
    ]),
    ...mapActions('commonOp', [
      'getBaseinfo'
    ]),
    toindex () {
      this.changednav('index')
      this.$router.push({ path: '/' })
    },
    togoodprice () {
      this.changednav('price')
      this.$router.push({ path: '/pages/goodprice/goodprice' })
    },
    toticket () {
      this.changednav('ticket')
      this.$router.push({ path: '/pages/goodticket/ticket' })
    },
    tolottery () {
      this.changednav('lottery')
      this.$router.push({ path: '/pages/lottery/lottery' })
    }
  }
}
</script>

<style lang="less" scoped>
@basecolor: #ff7500;

.buttom {
  width: 100%;
  min-width: 1200px;
  height: 294px;
  background-color: #f1f1f1;
  &__main {
    padding-top: 40px;
    width: 1000px;
    height: 234px;
    display: flex;
    margin: 0 auto;
    border-bottom: solid 1px #bfbfbf;
  }
  &__left {
    margin-left: 282px;
    &-webname {
      cursor: pointer;
      margin-bottom: 10px;
      width: 300px;
      text-align: left;
      font-size: 22px;
      color: @basecolor;
    }
    &-item {
      width: 300px;
      height: 30px;
      line-height: 30px;
      &-list {
        cursor: pointer;
        font-weight: 600;
        text-align: left;
        font-size: 16px;
        color: #2b2b2b;
      }
    }
  }
  &__right {
    &-contact {
      font-size: 20px;
      color: #333;
      font-weight: 600;
      margin-bottom: 22px;
      span {
        color: @basecolor;
      }
    }
    &-qrcode {
      width: 80px;
      height: 80px;
      background-color: #333;
      img {
        width: 80px;
        height: 80px;
      }
    }
  }
  &__reference {
    font-size: 12px;
    color: #737373;
    height: 60px;
    line-height: 60px;
  }
}
</style>



