import Vue from 'vue'
import Vuex from 'vuex'
import createLogger from 'vuex/dist/logger' // 控制台输出当前变化的某个状态
import commonOp from './common/opstate'
import login from './user/login'
import goodlist from './goodprice/goods'
import ticketlist from './goodticket/ticket'
import lotterylist from './lottery/lottery'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production' // 生产或开发环境打包

export default new Vuex.Store({
  modules: {
    commonOp,
    login,
    goodlist,
    ticketlist,
    lotterylist
  },
  strict: debug, // 按照官网建议，改变state的状态只能通过getter
  plugins: debug ? [createLogger()] : []
})



