import axios from '../../axios'
import { Message } from 'element-ui'

export default {
  namespaced: true,
  state: {
    userinfo: {
      isFetching: true,
      payload: []
    },
    isAuth: {
      faId: 0,
      label: ''
    },
    token: '',
    expiresIn: 0,
    loginState: -400,
    getDefaultState: () => {
      const userinfo = {
        isFetching: true,
        payload: []
      }
      const token = ''
      const expiresIn = 0
      const loginState = -400
      return {
        userinfo,
        token,
        expiresIn,
        loginState
      }
    }
  },
  mutations: {
    resetloginState (state) {
      Object.assign(state, state.getDefaultState())
    },
    updatingLoginInfo (state) {
      state.userinfo.isFetching = true
    },
    updatedLoginInfo (state, payload) {
      state.userinfo.isFetching = false
      state.loginState = payload.loginState
      state.token = payload.token
      state.expiresIn = payload.expiresIn
      state.userinfo.payload = payload.userinfo
    },
    changedFapro (state, payload) {
      state.isAuth.faId = payload.id
      state.isAuth.label = payload.label
    }
  },
  actions: {
    async login ({commit}, code) {
      commit('updatingLoginInfo')
      console.log('当前的code' + code)
      let res = await axios.post('/user/pc/login', { code: code })
      if (res.data.code === 0) {
        Message({
          message: '登录成功！',
          showClose: true,
          type: 'success'
        })
        let loginState = res.data.code
        let token = res.data.data.token.token
        let expiresIn = res.data.data.token.expires_in
        let userinfo = res.data.data.userinfo
        commit('updatedLoginInfo', {loginState, token, expiresIn, userinfo})
      }
      console.log(res)
      return res
    }
  }
}



