<template>
  <div class="goodprice">
    <headpage></headpage>
    <div v-if="showtoast" class="goodprice__mask"></div>
    <div v-if="showtoast" class="goodprice__toast"><toastbox></toastbox></div>
    <div class="goodprice__body">
    <div class="goodprice__searchbox">
      <div class="goodprice__searchbox-input"><input placeholder="请输入关键字" type="search" :value="keyword" @input="searchkey" @keyup.enter="pressEnter" /></div>
      <div class="goodprice__searchbox-btn" @click="searchHandle"><el-button>搜索</el-button></div>
    </div>
    <div class="goodprice__shift">
      <div class="goodprice__shift-sort">
        <div class="goodprice__shift-sort-title">商品分类:</div>
        <div class="goodprice__shift-sort-btn">
          <el-button size="small">全部</el-button>
          <el-button size="small">家电</el-button>
          <el-button size="small">数码</el-button>
          <el-button size="small">服饰</el-button>
          <el-button size="small">美食</el-button>
          <el-button size="small">美妆</el-button>
          <el-button size="small">宠物</el-button>
        </div>
      </div>
      <div class="goodprice__shift-sort">
        <div class="goodprice__shift-sort-title">商品排序:</div>
        <div class="goodprice__shift-sort-btn">
          <el-button size="small" @click="changedsort(1)" :class="type===1? 'sortactive': ''">按最新</el-button>
          <el-button size="small" @click="changedsort(2)" :class="type===2? 'sortactive': ''">按折扣</el-button>
          <el-button size="small" @click="changedsort(3)" :class="type===3? 'sortactive': ''">按省钱</el-button>
        </div>
      </div>
    </div>
    <div class="goodprice__list" v-if="commodities.payload.length!==0">
      <div class="goodprice__list-not" v-for="(row,i) in commodities.payload" :key="i">
        <div
          class="goodprice__list-item"
          v-for="(cell,j) in row"
          :key="j"
          :class="((j+1) % 4 === 0)?'noright':''"
          @mouseenter="mouseEnter(cell.sku)"
          @mouseleave="mouseLeave"
        >
          <img :src="cell.img" />
          <div class="goodprice__list-item-group">
            <div class="goodprice__list-item-group-title">{{cell.title}}</div>
            <div class="goodprice__list-item-group-bottom">
              <div class="discount">
                <div class="discount-percent">{{cell.discountpercent}}折</div>
                <div class="discount-saved"><span>节省 <i class="fa fa-jpy" aria-hidden="true"></i> {{(cell.jd_price-cell.price_now).toFixed(1)}}</span></div>
              </div>
              <div class="money">
                <div class="money-now">原价：<i class="fa fa-jpy" aria-hidden="true"></i>{{cell.jd_price}}</div>
                <div class="money-history">最低价：{{cell.price_now}}元</div>
              </div>
            </div>
          </div>
          <div class="mask" v-if="maskarr.isEnter && maskarr.goodID === cell.sku"></div>
          <div class="mask__detail" v-if="maskarr.isEnter && maskarr.goodID === cell.sku">
            <div class="mask__detail-title">{{cell.title}}</div>
            <div class="mask__detail-text">原价：<i class="fa fa-jpy" aria-hidden="true"></i>{{cell.jd_price}}</div>
            <div class="mask__detail-text">用券最低价：<span><i class="fa fa-jpy" aria-hidden="true"></i>{{cell.price_now}}</span></div>
            <div class="mask__detail-text">帮您节省：<span><i class="fa fa-jpy" aria-hidden="true"></i>{{(cell.jd_price-cell.price_now).toFixed(1)}}</span></div>
            <div class="mask__detail-text">帮您折扣：<span>{{cell.discountpercent}}折</span></div>
            <div class="mask__detail-btn">
              <el-button class="mask__detail-btn-btn1" @click="gooddetail(cell.url)">产品详情</el-button>
              <el-button class="mask__detail-btn-btn2" @click="showtoastbox">神价入手</el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="goodprice__pagination" v-if="commodities.payload.length > 0">
      <ul class="pagination">
        <li class="previous btn" @click="prePage" v-if="page>1">上一页</li>
        <li class="pages" :class="page===num-3?'pageActive':''" v-if="(num-3)<=commodities.pageCount">{{num-3}}</li>
        <li class="pages" :class="page===num-2?'pageActive':''" v-if="(num-2)<=commodities.pageCount">{{num-2}}</li>
        <li class="pages" :class="page===num-1?'pageActive':''" v-if="(num-1)<=commodities.pageCount">{{num-1}}</li>
        <li class="pages" :class="page===num?'pageActive':''" v-if="num<=commodities.pageCount">{{num}}</li>
        <li class="pages" :class="page===num+1?'pageActive':''" v-if="(num+1)<=commodities.pageCount">{{num+1}}</li>
        <li class="pages" :class="page===num+2?'pageActive':''" v-if="(num+2)<=commodities.pageCount">{{num+2}}</li>
        <li class="next btn" @click="nextPage" v-if="page<commodities.pageCount">下一页</li>
      </ul>
    </div>
    <div class="goodprice__list-empty" v-if="commodities.payload.length===0">
      <div class="goodprice__list-empty-tip">抱歉，你搜索的商品暂时没有找到哦~</div>
      <div class="goodprice__list-empty-recommend">
        <div class="goodprice__list-empty-recommend-title">推荐神价商品</div>
        <div class="goodprice__list-empty-recommend-line"></div>
        <div class="goodprice__list-empty-recommend-list" v-for="(row,i) in recommendList.payload" :key="i">
          <div
            class="goodprice__list-item"
            v-for="(cell,j) in row"
            :key="j"
            :class="((j+1) % 4 === 0)?'noright':''"
            @mouseenter="mouseEnter(cell.sku)"
            @mouseleave="mouseLeave"
          >
            <img :src="cell.img" />
            <div class="goodprice__list-item-group">
              <div class="goodprice__list-item-group-title">{{cell.title}}</div>
              <div class="goodprice__list-item-group-bottom">
                <div class="discount">
                  <div class="discount-percent">{{cell.discountpercent}}折</div>
                  <div class="discount-saved"><span>节省 <i class="fa fa-jpy" aria-hidden="true"></i> {{(cell.jd_price-cell.price_now).toFixed(1)}}</span></div>
                </div>
                <div class="money">
                  <div class="money-now">原价：<i class="fa fa-jpy" aria-hidden="true"></i>{{cell.jd_price}}</div>
                  <div class="money-history">最低价：{{cell.price_now}}元</div>
                </div>
              </div>
            </div>
            <div class="mask" v-if="maskarr.isEnter && maskarr.goodID === cell.sku"></div>
            <div class="mask__detail" v-if="maskarr.isEnter && maskarr.goodID === cell.sku">
              <div class="mask__detail-title">{{cell.title}}</div>
              <div class="mask__detail-text">原价：<i class="fa fa-jpy" aria-hidden="true"></i>{{cell.jd_price}}</div>
              <div class="mask__detail-text">用券最低价：<span><i class="fa fa-jpy" aria-hidden="true"></i>{{cell.price_now}}</span></div>
              <div class="mask__detail-text">帮您节省：<span><i class="fa fa-jpy" aria-hidden="true"></i>{{(cell.jd_price-cell.price_now).toFixed(1)}}</span></div>
              <div class="mask__detail-text">帮您折扣：<span>{{cell.discountpercent}}折</span></div>
              <div class="mask__detail-btn">
                <el-button class="mask__detail-btn-btn1" @click="gooddetail(cell.url)">产品详情</el-button>
                <el-button class="mask__detail-btn-btn2" @click="showtoastbox">神价入手</el-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <buttompage></buttompage>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import headpage from '@/components/head'
import buttompage from '@/components/buttom'
import toastbox from '@/components/toastbox'

export default {
  created () {
    console.log('进入神价页')
    this.changedgoodCount(28)
    this.reloadCommodities().catch(err => {
      console.log(err)
      if (err.message.indexOf('timeout') !== -1) {
        this.$router.push({
          path: '../notfind'
        })
      }
    })
  },
  components: {
    headpage,
    buttompage,
    toastbox
  },
  computed: {
    ...mapState('goodlist', [
      'maskarr',
      'commodities',
      'recommendList',
      'commoditiesSort',
      'showtoast',
      'keyword',
      'type',
      'page',
      'num'
    ])
  },
  methods: {
    ...mapMutations('goodlist', [
      'enterItem',
      'leaveItem',
      'showtipbox',
      'updategoodKeyword',
      'updateSort',
      'changedgoodCount'
    ]),
    ...mapActions('goodlist', [
      'reloadCommodities',
      'loadNextCommodities',
      'loadPreCommodities'
    ]),
    searchkey (e) {
      console.log(e.target.value)
      this.updategoodKeyword(e.target.value)
    },
    pressEnter (e) {
      let keyCode = window.event ? e.keyCode : e.which
      console.log('回车搜索', keyCode, e)
      if (keyCode === 13) {
        console.log('执行搜索')
        this.searchHandle()
      }
    },
    searchHandle () {
      this.changedgoodCount(28)
      this.reloadCommodities()
    },
    changedsort (sortid) {
      this.updateSort(sortid)
      this.reloadCommodities()
    },
    mouseEnter (id) {
      this.enterItem(id)
    },
    mouseLeave () {
      this.leaveItem()
    },
    gooddetail (url) {
      console.log(url)
      window.open(url, '_blank')
    },
    showtoastbox () {
      this.showtipbox()
    },
    prePage () {
      this.loadPreCommodities()
    },
    nextPage () {
      this.loadNextCommodities()
    }
  }
}
</script>

<style lang="less" scoped>
@basecolor: #ff7500;

.goodprice {
  width: 100%;
  min-width: 1300px;
  background-color: #fff;
  &__mask {
    position: fixed;
    width: 100%;
    min-width: 1300px;
    z-index: 100;
    height: 100%;
    opacity: 0.8;
    background-color:#3a3a3a;
  }
  &__toast {
    position: absolute;
    top: 200px;
    z-index: 101;
    width: 100%;
    min-width: 1300px;
  }
  &__body {
    position: absolute;
    top: 100px;
    width: 100%;
    min-width: 1300px;
  }
  &__searchbox {
    margin: 20px auto;
    width: 795px;
    height: 50px;
    border-radius: 10px;
    display: flex;
    border-radius: 10px;
    border: solid 1px #eee;
    &-input {
      padding-left: 8px;
    }
    input {
      font-size: 16px;
      width: 660px;
      text-align: center;
      height: 48px;
      padding: 3px 5px;
      border: none;
    }
    input::placeholder {
      color: #bebebe;
    }
    &-btn {
      width: 120px;
      height: 48px;
      background-color: @basecolor;
    }
    button {
      margin-left: 8px;
      padding-left: 10px;
      width: 120px;
      height: 48px;
      color: #fff;
      border: none;
      background-color: @basecolor;
      border-radius: 10px;
    }
  }
  &__shift {
    width: 1203px;
    margin: 0 auto 0 auto;
    &-sort {
      margin-top: 24px;
      margin-left: 20px;
      display: flex;
      font-size: 16px;
      color: #979797;
      &-title {
        width: 85px;
        text-align: left;
      }
      &-btn {
        button {
          font-size: 16px;
          color: #979797;
          padding: 0 0;
          border: none;
        }
        button:hover, .sortactive {
          color: @basecolor;
          background-color: #fff;
        }
      }
    }
  }
  &__list {
    width: 1203px;
    margin: 32px auto 0 auto;
    border-top: solid 1px #bebebe;
    &-not {
      width: 1203px;
      display: flex;
      padding: 20px 0;
    }
    .noright {
      margin-right: 0;
    }
    &-item {
      width: 282px;
      height: 420px;
      margin-right: 25px;
      .mask {
        width: 282px;
        height: 420px;
        margin-top: -428px;
        opacity: 0.8;
        background-color: #3a3a3a;
        &__detail {
          position: absolute;
          z-index: 100;
          width: 282px;
          height: 420px;
          margin-top: -420px;
          &-title {
            width: 220px;
            margin: 100px auto 10px auto;
            font-size: 14px;
            color: #fff;
            text-align: left;
          }
          &-text {
            width: 220px;
            margin: 8px auto 0 auto;
            font-size: 14px;
            color: #fff;
            text-align: left;
            span {
              color: @basecolor;
            }
          }
          &-btn {
            margin-top: 50px;
            &-btn1 {
              font-size: 16px;
              border: solid 1px @basecolor;
              color: @basecolor;
              border-radius: 10px;
              opacity: 0.8;
              background-color: #686464;
            }
            &-btn2 {
              font-size: 16px;
              border: solid 1px @basecolor;
              background-color: @basecolor;
              color: #fff;
              border-radius: 10px;
            }
          }
        }
      }
      img {
        border: solid 1px #bfbfbf;
        width: 282px;
        height: 282px;
      }
      &-group {
        padding: 25px 5px 23px 5px;
        &-title {
          width: 282px;
          height: 42px;
          display: -webkit-box;
          overflow: hidden;
          text-overflow: ellipsis;
          -o-text-overflow: ellipsis;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          text-align: left;
          font-size: 14px;
          color: #323232;
          padding-bottom: 5px;
        }
        &-bottom {
          display: flex;
          .discount {
            width: 110px;
            &-percent {
              text-align: left;
              font-size: 17px;
              color: @basecolor;
            }
            &-saved {
              span {
                padding: 5px 5px;
                background-color: @basecolor;
              }
              text-align: left;
              height: 25px;
              color: #fff;
              line-height: 25px;
              font-size: 13px;
              padding: 0 3px;
            }
          }
          .money {
            width: 160px;
            &-now {
              text-align: right;
              color: #9f9f9f;
              font-size: 12px;
            }
            &-history {
              margin-top: 10px;
              text-align: right;
              color: @basecolor;
              font-size: 18px;
            }
          }
        }
      }
    }
    &-empty {
      width: 1300px;
      margin: 20px auto;
      border-top: solid 1px #bebebe;
      &-tip {
        margin: 44px 0;
        font-size: 20px;
        color: #555;
        text-align: left;
      }
      &-recommend {
        &-list {
          display: flex;
        }
        &-title {
          font-size: 26px;
          color: #2d2d2d;
          text-align: left;
        }
        &-line {
          margin: 5px 0 18px 0;
          width: 55px;
          height: 5px;
          background-color: @basecolor;
          border-radius: 5px;
        }
      }
    }
  }
  &__pagination {
    height: 80px;
    .pagination {
      .btn {
        font-size: 14px;
        padding: 10px 20px;
        border-radius: 10px;
      }
      .previous {
        border: solid 1px #333;
        background-color: #fff;
        color: #333;
      }
      .previous:hover {
        border: solid 1px @basecolor;
        color: @basecolor;
      }
      .next {
        color: #fff;
        background-color: @basecolor;
      }
      .pages {
        font-size: 14px;
        padding: 8px 15px;
        color: #333;
      }
      .pages:hover, .pageActive {
        font-size: 14px;
        padding: 8px 15px;
        color: @basecolor;
      }
    }
  }
}
</style>



