// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Vuex from 'vuex'
import App from './App'
import router from './router/router'
import ElementUI from 'element-ui'
import axios from './axios'
import $ from 'jquery'
import QRCode from 'qrcodejs2'
import wx from 'weixin-js-sdk'
import 'bootstrap/dist/js/bootstrap.min'
import './assets/element-variables.scss'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'font-awesome/css/font-awesome.min.css'
import store from './store/store'
import QS from 'qs'
import wxlogin from 'vue-wxlogin'

Vue.config.productionTip = false
Vue.use(ElementUI)
Vue.use(Vuex)
Vue.use(wxlogin)

Vue.prototype.$http = axios
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  QRCode,
  wx,
  $,
  components: { App },
  template: '<App/>'
})

// http请求拦截器
axios.interceptors.request.use(config => {
  config.data = QS.stringify(config.data) // 防止post请求参数无法传到后台,在这里就用到了qs
  config.headers = {
    'Content-Type': 'application/x-www-form-urlencoded'
  }
  ElementUI.Loading.service({ // 使用element-ui的加载条组件
    fullscreen: true,
    text: '拼命加载中...'
  })
  return config
}, error => {
  let loading = ElementUI.Loading.service({})
  loading.close() // 关闭加载前，记得重新定义实例
  if (error.message.indexOf('timeout') !== -1) {
    console.log('拦截request发现请求超时')
  }
  return Promise.reject(error)
})
// http响应拦截器
axios.interceptors.response.use(data => {
  let loading = ElementUI.Loading.service({})
  loading.close()
  return data
}, error => {
  let loading = ElementUI.Loading.service({
    fullscreen: true,
    text: '拼命加载中...'
  })
  loading.close()
  if (error.message.indexOf('timeout') !== -1) {
    console.log('拦截response发现请求超时')
  }
  return Promise.reject(error)
})



