import axios from '../../axios'
import { MessageBox } from 'element-ui'

export default {
  namespaced: true,
  state: {
    lotteryList: {
      isFetching: true,
      pageCount: 0,
      payload: []
    },
    lotteryUrl: {
      isFetching: true,
      url: ''
    },
    keyword: '',
    page: 1,
    num: 4,
    count: 8,
    getDefaultState: () => {
      const lotteryList = {
        isFetching: true,
        pageCount: 0,
        payload: []
      }
      const keyword = ''
      const type = 1
      const page = 1
      const num = 4
      const count = 8
      return {
        lotteryList,
        keyword,
        type,
        page,
        num,
        count
      }
    }
  },
  mutations: {
    resetLotteryState (state) {
      Object.assign(state, state.getDefaultState())
    },
    reloadingLotteryList (state) {
      state.page = 1
      state.lotteryList = {
        isFetching: true,
        pageCount: 0,
        payload: []
      }
    },
    loadingNextLottery (state) {
      state.page = state.page + 1
      state.lotteryList.isFetching = true
      if (state.page === state.num && (state.page + 4) < state.lotteryList.pageCount) {
        state.num += 1
      }
    },
    loadingPreLottery (state) {
      state.page = state.page - 1
      state.lotteryList.isFetching = true
      if (state.num > 4) {
        state.num -= 1
      }
    },
    loadedLotteryList (state, payload) {
      state.lotteryList.isFetching = false
      state.lotteryList.pageCount = payload.pageCount
      state.lotteryList.payload = payload.lotteryList
    },
    updatelotteryKeyword (state, keyword) {
      state.keyword = keyword
    },
    changedlotteryCount (state, count) {
      state.count = count
    },
    gettingUrl (state) {
      state.lotteryUrl = {
        isFetching: true,
        url: ''
      }
    },
    gettedUrl (state, url) {
      state.lotteryUrl.isFetching = false
      state.lotteryUrl.url = url
    }
  },
  actions: {
    async reloadLotteryList ({ commit, state }) {
      commit('reloadingLotteryList')
      let res = await axios.get('/lottery/search?content=' + state.keyword + '&page=' + state.page + '&count=' + state.count)
      let result = res.data.data.lotteries
      let pageCount = res.data.data.pages
      let arrTemp = []
      let index = 0
      let sectionCount = 3
      for (var i = 0; i < result.length; i++) {
        index = parseInt(i / sectionCount)
        if (arrTemp.length <= index) {
          arrTemp.push([])
        }
        arrTemp[index].push(result[i])
      }
      console.log(arrTemp)
      commit('loadedLotteryList', {
        pageCount,
        lotteryList: Array.isArray(arrTemp)
          ? arrTemp
          : []
      })
      console.log(res)
      return res
    },
    async loadNextLottery ({ commit, state }) {
      if (state.page === state.lotteryList.pageCount) {
        return
      }
      commit('loadingNextLottery')
      let res = await axios.get('/lottery/search?content=' + state.keyword + '&page=' + state.page + '&count=' + state.count)
      console.log(res)
      let result = res.data.data.lotteries
      let pageCount = res.data.data.pages
      let arrTemp = []
      let index = 0
      let sectionCount = 3
      for (var i = 0; i < result.length; i++) {
        index = parseInt(i / sectionCount)
        if (arrTemp.length <= index) {
          arrTemp.push([])
        }
        arrTemp[index].push(result[i])
      }
      console.log(arrTemp)
      commit('loadedLotteryList', {
        pageCount,
        lotteryList: Array.isArray(arrTemp)
          ? arrTemp
          : []
      })
      return res
    },
    async loadPreLottery ({ commit, state }) {
      if (state.page === 1) {
        return
      }
      commit('loadingPreLottery')
      let res = await axios.get('/lottery/search?content=' + state.keyword + '&page=' + state.page + '&count=' + state.count)
      console.log(res)
      let result = res.data.data.lotteries
      let pageCount = res.data.data.pages
      let arrTemp = []
      let index = 0
      let sectionCount = 3
      for (var i = 0; i < result.length; i++) {
        index = parseInt(i / sectionCount)
        if (arrTemp.length <= index) {
          arrTemp.push([])
        }
        arrTemp[index].push(result[i])
      }
      console.log(arrTemp)
      commit('loadedLotteryList', {
        pageCount,
        lotteryList: Array.isArray(arrTemp)
          ? arrTemp
          : []
      })
      return res
    },
    async requiredLottery ({commit}, {lotteryCode, token}) {
      commit('gettingUrl')
      let res = await axios.post('/lottery/pc/unlock?token=' + token, {lotteryCode})
      if (res.data.code === 0) {
        let url = res.data.data
        commit('gettedUrl', url)
        window.open(url, '_blank')
      } else {
        MessageBox({
          message: '页面获取失败',
          showClose: true,
          type: 'error'
        })
      }
      console.log(res)
      return res
    }
  }
}



