import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import goodprice from '@/pages/goodprice/goodprice'
import goodticket from '@/pages/goodticket/ticket'
import lottery from '@/pages/lottery/lottery'
import login from '@/pages/login'
import notfind from '@/pages/notfind'

Vue.use(Router)

export default new Router({
  base: '/pc/',
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/pages/notfind',
      name: 'notfind',
      component: notfind
    },
    {
      path: '/pages/login',
      name: 'login',
      component: login
    },
    {
      path: '/pages/goodprice/goodprice',
      name: 'goodprice',
      component: goodprice
    },
    {
      path: '/pages/goodticket/ticket',
      name: 'goodticket',
      component: goodticket
    },
    {
      path: '/pages/lottery/lottery',
      name: 'lottery',
      component: lottery
    }
  ]
})



