import axios from '../../axios'

export default {
  namespaced: true,
  state: {
    baseinfo: {
      isFetching: true,
      payload: []
    },
    navstate: 'index',
    searchcommand: 'goods',
    searchrange: '找好价',
    keyword: '',
    getDefaultState: () => {
      const navstate = 'index'
      const searchcommand = 'goods'
      const searchrange = '找好价'
      const keyword = ''
      return {
        navstate,
        searchcommand,
        searchrange,
        keyword
      }
    }
  },
  mutations: {
    resetCommonState (state) {
      Object.assign(state, state.getDefaultState())
    },
    changednav (state, payload) {
      state.navstate = payload
    },
    changedsearchRange (state, text) {
      state.searchcommand = text
      if (text === 'goods') {
        state.searchrange = '找好价'
      } else if (text === 'ticket') {
        state.searchrange = '找好券'
      }
    },
    gainningBaseinfo (state) {
      state.baseinfo.isFetching = false
    },
    gainnedBaseinfo (state, payload) {
      state.baseinfo.isFetching = true
      state.baseinfo.payload = payload.baseinfo
    }
  },
  actions: {
    async getBaseinfo ({commit}) {
      commit('gainningBaseinfo')
      let res = await axios.get('/pc/info')
      commit('gainnedBaseinfo', {
        baseinfo: res.data.data
      })
      return res
    }
  }
}



